@import '../../../style/App.scss';



.contact__social {
  width: 100%;
  margin: 60px 0 10px 0;
  transition: 0.1s ease;
  cursor: pointer;
}

i {
  color: rgb(26, 26, 26);
}

i:hover {
  color: rgb(235, 161, 173)
}






@media screen and (max-width: 990px) {


  .contact__social {
    text-align: center;
    width: 100%;
    img {
      margin: 0 auto;
    }
  }
}